import React, { Component } from 'react'

export default class Introduction extends Component{
    render() {
        return (
            <div>
                <section id="colorlib-hero" className="js-fullheight" data-section="home">
                    <div className="flexslider js-fullheight">
                        <ul className="slides">
                            <li style={{backgroundImage:'url(images/img_bg_1.jpg)'}} className="flex-active-slide" data-thumb-alt="">
                                <div className="overlay"></div>
                                <div className="container-fluid">
                                    <div className="row">
                                        <div className="col-md-6 col-md-offset-3 col-md-pull-3 col-sm-12 col-xs-12 js-fullheight slider-text animated fadeInUp">
                                            <div className="slider-text-inner js-fullheight">
                                                <div className="desc">
                                                    <h1>Hi! <br />I'm Siva</h1>
                                                    <h2>100% html5 bootstrap templates Made by <a href="https://colorlib.com/" target="_blank" rel="noreferrer">colorlib.com</a></h2>
                                                    <p><a href="https://colorlib.com/" rel="noreferrer" className="btn btn-primary btn-learn">Download CV <i className="icon-download4"></i></a></p>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </li>
                            <li style={{backgroundImage: 'url(images/img_bg_2.jpg)'}} data-thumb-alt="" className="">
                                <div className="overlay"></div>
                                <div className="container-fluid">
                                    <div className="row">
                                        <div className="col-md-6 col-md-offset-3 col-md-pull-3 col-sm-12 col-xs-12 js-fullheight slider-text">
                                            <div className="slider-text-inner">
                                                <div className="desc">
                                                    <h1>I am <br />a Designer</h1>
                                                    <h2>100% html5 bootstrap templates Made by <a href="https://colorlib.com/" target="_blank" rel="noreferrer">colorlib.com</a></h2>
                                                    <p><a className="btn btn-primary btn-learn" href="https://colorlib.com/">View Portfolio <i className="icon-briefcase3"></i></a></p>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </li>
                        </ul>
                        <ol className="flex-control-nav flex-control-paging">
                            <li><a href="https://colorlib.com/" className="flex-active">1</a></li>
                            <li><a href="https://colorlib.com/" className="">2</a></li>
                        </ol>
                        <ul className="flex-direction-nav">
                            <li className="flex-nav-prev"><a href="https://colorlib.com/" className="flex-prev" >Previous</a></li>
                            <li className="flex-nav-next"><a href="https://colorlib.com/" className="flex-next" >Next</a></li>
                        </ul>
                    </div>
                </section>
            </div>
        )
    }
}